.contact-details{
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 5%;
    padding-left: 5%;
    /* overflow: hidden; */
    /* padding: 15%; */

}
.info-box{
    display:flex;
    gap: 10%;
    width: 90%;
    justify-content: space-between;

}
.contact-details a{
    text-decoration: none;
    color: white;
    font-size: large;
}
.contact-details h4{
    color: rgb(195, 191, 191);
    margin-bottom: 5px;
}
.email-phone{
    display: flex;
    flex-direction: column;
    gap: 5%;
}
.design-develop{
    font-size: large;
}
.design-develop span span{
    color: rgb(185, 42, 210);
    font-weight: bold;
}
.design-develop p{
    font-size: medium;
    font-weight: 100;
}
/* .balls{
    height: 40%;
    width: 100%;
} */
@media screen and (max-width: 768px) {
    .contact-details{
        margin-left: 5px;
    }
       .info-box{
        flex-direction: column;
        gap: 20px;
        

       }
       .design-develop p{
    font-size: small;
    /* background-color: grey; */

}
}